/*
подключение файлов стилей блоков и элементов
**********************************/
@import "vars";
@import "libs";
@import "layout/index";
@import "components/index";

/*
подключение глобальных стилей 
**********************************/
body {
	color: #ffffff;
	font-family: "Roboto", sans-serif;
	position: relative;
	

	&.primary {
		background: #14131a url("../img/background.jpg") no-repeat center / 100%;
	}

	&.body-blood {
		background: url("../img/blood.png") no-repeat top right / 70%,
			#14131a url("../img/background.jpg") no-repeat center / 100%;
	}

	.venom {
		position: absolute;
		right: 0;
		top: 0;
		max-width: 728px;
		width: 50%;
	}
}
.container {
	max-width: 1160px;
	width: 100%;
	padding: 0 10px;
	margin: 0 auto;
}
@import "media";
