.content {
	margin-top: 5%;
	color: rgba(255, 255, 255, 0.8);
	.row {
		display: flex;
	}
	&__col {
		flex: 0 50%;
		font-size: 16px;
		line-height: 140%;
		&:not(:last-child) {
			margin-right: 30px;
		}
	}

	&__title {
		font-weight: bold;
		font-size: 36px;
		margin-bottom: 21px;
	}

	&__text {
		max-width: 423px;
		margin-bottom: 30px;
	}

	&__subtitle {
		margin-bottom: 15px;
	}

	&__list {
		&.col-2 {
			-moz-column-count: 2; /* Для Firefox */
			-webkit-column-count: 2; /* Для Safari и Chrome */
			column-count: 2;
			-moz-column-gap: 20px;
			-webkit-column-gap: 20px;
			column-gap: 20px;
			margin-bottom: 33px;
			max-width: 320px;
		}
	}

	&__item {
		line-height: 140%;
	}

	&__preview {
		position: relative;
		max-width: 356px;
		height: 205px;
		border-radius: 5px;
		background: url("/img/preview.jpg") no-repeat center / contain;
	}

	&__play {
		background: #ffffff;
		opacity: 0.7;
		cursor: pointer;
		width: 45px;
		height: 45px;
		border: none;
		border-radius: 50%;
		position: absolute;
		top: 50%;
		left: 50%;
		padding: 14px 0 16px;
		transform: translateY(-50%) translateX(-50%);
		text-align: center;
	}

	&__poster-wrapper {
		display: flex;
		h3 {
			margin-bottom: 5px;
		}
		ul:not(:last-child) {
			margin-bottom: 20px;
		}
	}
	&__poster {
		margin-right: 30px;
		border-radius: 5px;
		max-width: 356px;
	}
}
