.header {
	&__top {
		padding-top: 40px;
		display: flex;
		justify-content: start;
		align-items: center;
	}

	&__logo {
		display: inline-block;
		margin-right: 52px;
	}
}
.hero {
	padding-top: 10%;
	color: rgba(255, 255, 255, 0.8);

	&__data {
		display: block;
		margin-bottom: 3%;
		font-size: 24px;
	}

	&__logo {
		margin-bottom: 3%;
		width: 70%;
	}

	&__description {
		font-size: 16px;
		line-height: 140%;
		margin-bottom: 5%;
		max-width: 423px;
	}

	&__button-group {
		display: flex;
		margin-bottom: 144px;
		.btn {
			padding: 15px 30px;
			display: inline-block;
			text-align: center;
			font-size: 16px;
			border-radius: 5px;
			cursor: pointer;
			&::before {
				content: "";
				display: inline-block;
				height: 15px;
				width: 13px;
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				margin-right: 10px;
				vertical-align: middle;
			}
		}
		.btn-primary {
			background-color: #ffffff;
			color: #8d0019;
			margin-right: 20px;
			&::before {
				border: none;
				background-image: url("/img/play.svg");
			}
		}
		.btn-link {
			color: #ffffff;
			&::before {
				background-image: url("/img/ticket.svg");
			}
		}
	}
}
