@media only screen and (max-width: 1124px) {
	.content {
		.row {
			flex-direction: column;
		}
		&__col:not(:last-child) {
			margin-right: 0px;
			margin-bottom: 30px;
		}
	}
}

@media only screen and (max-width: 768px) {
	body {
		&.body-blood {
			background: #14131a;
		}

		img.venom {
			top: 15%;
		}
	}
}
@media only screen and (max-width: 576px) {
	.header__top {
		padding-top: 15px;
	}
	.nav {
		margin-left: auto;

		&__item:not(:last-child) {
			margin-right: 0px;
			margin-bottom: 15px;
		}
		&__link {
			color: #000000;
		}
		&__menu {
			flex-direction: column;
			align-items: center;
			justify-content: center;
			position: fixed;
			left: 0;
			top: 0;
			background-color: #ffffff;
			height: 100%;
			width: 100%;
			z-index: 4;
			transform: translateX(100%);
			transition: transform 0.2s;
		}

		&__burger {
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 10px;
		}
		.burger {
			background: none;
			&__text {
				margin-right: 11px;
				color: rgba(255, 255, 255, 0.5);
			}
		}

		.close {
			&__text {
				margin-right: 11px;
			}
			opacity: 0;
			background-color: #ffffff;
			position: fixed;
			right: 25px;
			top: 42px;
			display: flex;
			align-items: center;
			justify-content: center;
			z-index: 9;
			color: #000000;
			border: none;
			cursor: pointer;
			transform: translateX(300%);
			transition: transform 0.2s;
			transition-delay: 0.2s;
		}
		.is-active {
			opacity: 1;
			transform: translateX(0%);
		}
	}
	.hero {
		&__text {
			text-align: center;
		}
		&__button-group {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			margin-top: 35px;
			.btn-primary {
				margin-bottom: 25px;
				margin-right: 0px;
				text-align: center;
			}
		}
	}
	.content {
		&__list {
			&.col-2 {
				column-count: 1;
			}
		}
		&__poster-wrapper {
			display: flex;
			flex-direction: column;
			img {
				margin-right: 0px;
				margin-bottom: 20px;
			}
		}
	}
}

@media only screen and (max-width: 480px) {
	body {
		img.venom {
			opacity: 0.5;
		}
	}
}
